nt-size:12px}.nweetEdit .formBtn{cursor:pointer;margin-top:15px;margin-bottom:5px}.nweet{margin-bottom:20px;background-color:#fff;width:100%;max-width:320px;padding:20px;border-radius:10px;position:relative;display:flex;flex-direction:column;color:rgba(0,0,0,.8);box-shadow:8px 8px 13px #e0e0e0,-8px -8px 13px #fff}.nweet h4{font-size:14px}.nweet img{width:100%;height:150px;object-fit:contain;margin-top:10px}.nweet__actions{position:absolute;right:10px;top:10px}.nweet__actions span{cursor:pointer}.nweet__actions span:first-child{margin-right:10px}.authContainer{display:flex;flex-direction:column;height:100vh;justify-content:center;align-items:center}.authBtns{display:flex;justify-content:space-between;width:100%;max-width:320px}.authBtn{cursor:pointer;border-radius:20px;border:none;padding:10px 0px;font-size:12px;text-align:center;width:150px;background:#fff;cursor:pointer;box-shadow:8px 8px 13px #e0e0e0,-8px -8px 13px #fff}.authInput{max-width:320px;width:100%;padding:10px;border-radius:30px;background-color:#fff;margin-bottom:10px;font-size:12px;color:#000;box-shadow:8px 8px 13px #e0e0e0,-8px -8px 13px #fff}.authSubmit{text-align:center;background:#04aaff;color:#fff;margin-top:10;cursor:pointer}.authError{color:tomato;text-align:center;font-weight:500;font-size:12px}.authSwitch{color:#04aaff;cursor:pointer;margin-top:10px;margin-bottom:50px;display:block;font-size:12px;text-decoration:underline}
